import React from 'react';

class BookList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            books: this.props.books
        }
    }

    render() {
        var renderBooks = this.state.books.map((id, b) => {
            <p>{b.author}</p>
        })

        return(
            <div>
                {renderBooks}
            </div>
        )
    }
}

export default BookList;


